import React from 'react'

const sassComponent = () => (
  <div>
    <h2>
      기존의 CSS코드를 좀 더 효율적으로 작성하기 위해
      <br />
      CSS 전처리기 SASS를 활용하여
      <br />
      개발 효율성을 높였습니다.
    </h2>
  </div>
)

export default sassComponent



